@import "./Variables.scss";

.hero{
    width: 100vw;
    height: 20%;
    position: relative;
    background: $white;
    box-sizing: border-box;
    z-index: 1;
   // border: 2px solid green;
    //background: #113263;

    @media (min-width: 500px) {
      height: 12%;
    }

    @media (min-width: 800px){
      height: 20%;
    }
    @media (min-width: 1025px) {
      height: 20%;
    }
    @media (min-width: 1800px){
      height: 20%;
    }

    .blob-1 {
      position: absolute;
      opacity: 0.4;
      width: 400px;
      aspect-ratio: 1/1;
      animation: animate 10s cubic-bezier(0.68, -0.55, 0.27, 1.55) infinite
        alternate forwards;
      filter: blur(40px);
      z-index: -1;
      background: linear-gradient(
        47deg,
        rgba(41, 204, 201) 21%,
        rgb(5 140 236) 67%,
        rgba(5, 184, 236) 80%
      )
    }
    .blob-2{
      position: absolute;
      top: 40%;
      right: 10%;
      opacity: 0.3;
      width: 360px;
      border-radius: 50%;
      aspect-ratio: 1/1;
      filter: blur(40px);
      z-index: -1;
      background: linear-gradient(90deg, #d53369 0%, #daae51 100%);
    }

    @keyframes animate {
      0% {
        translate: 40% -25%;
        border-radius: 60% 40% 30% 70% / 100% 85% 92% 74%;
      }
      50% {
        translate: 0% 13%;
        border-radius: 20% 71% 47% 70% / 81% 15% 22% 54%;
        rotate: 41deg;
        scale: 1.15;
      }

      100% {
        translate: -45% 39%;
        border-radius: 100% 75% 92% 74% / 60% 80% 30% 70%;
        rotate: -60deg;
        scale: 1.05;
      }
    }
    

    .hero-container{
      width: 80%;
      height: 45%;
      position: relative;
      top: 10%;
      left: 10%;
      border-radius: 0.7rem;
      z-index: 1;
      backdrop-filter: blur(20px) saturate(188%);
      -webkit-backdrop-filter: blur(20px) saturate(188%);
      background-color: rgba(242, 244, 248, 0.45);
      border-radius: 12px;
      border: 1px solid rgba(255, 255, 255, 0.125);
     // background: $arcticGrey;
     // backdrop-filter: saturate(180%) blur(20px);

     @media (min-width: 800px) {
     // height: 65%;
    }
    @media (min-width: 1025px) {
      width: 70%;
      height: 45%;
      top: 10%;
      left: 15%;
      
    }
    @media (min-width: 1100px) {
      
    }
    @media (min-width: 1800px) {
      width: 60%;
      height: 40%;
      top: 10%;
      left: 20%;
      
    }
     
     
  .hero-content{
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 0.7rem;
    background-color: inherit;
   // background-color: $arcticGrey;


  .hero-hello{
        font-size: 2.2rem;
        position: absolute;
        top: 4%;
        left: 1.5%;

        @media (min-width: 500px) {
          font-size: 3.8rem;
      
        }
        @media (min-width: 1025px) {
          font-size: 3.5rem;
          top: 4%;
          left: 1.5%;
        }
        @media (min-width: 1100px) {
          
        }
        @media (min-width: 1800px) {
          font-size: 5rem;
        }
      }
  
    .hero-title{
      font-family: "Roboto Condensed", sans-serif;
      font-size: 1.5rem;
      font-weight: 300;
      background: rgb(128,130,136);
     background: linear-gradient(90deg, rgba(128,130,136,1) 0%, rgba(68,70,74,1) 67%);
     -webkit-background-clip: text;
     -webkit-text-fill-color: transparent;
      position: absolute;
      top: 6%;
      left: 16%;
  
      @media (min-width: 500px) {
          font-size: 2rem;
          top: 10%;
          left: 10%;
        }
        @media (min-width: 1025px) {
          font-size: 1.8rem;
          font-weight: 500;
          top: 8%;
          left: 9%;
        }
        @media (min-width: 1500px) {
          font-size: 2rem;
        }
        @media (min-width: 1900px) {
          font-size: 4rem;
        }
  
  }
  
  .hero-title.darkmode{
    color: $white;
  }
  
  .hero-copy{
    font-family: "Inter", sans-serif;
      font-size: 1.8rem;
      font-weight: 300;
     // color: $darkGreyFont;
     background: rgb(128,130,136);
     background: linear-gradient(90deg, rgba(128,130,136,1) 0%, rgba(68,70,74,1) 67%);
     -webkit-background-clip: text;
     -webkit-text-fill-color: transparent;
      position: absolute;
      top: 20%;
      left: 10%;
      line-height: normal;
  
      @media (min-width: 500px) {
        font-size: 1.7rem;
        top: 40%;
//border: 2px solid red;
        width: 80%;
      }
      @media (min-width: 1025px) {
        width: 90%;
        //border: 2px solid red;
        font-size: 0.9rem;
        font-weight: 400;
        top: 22%;
        left: 9%;
      }
      @media (min-width: 1100px) {
        font-size: 2.6rem;
        font-weight: 600;
      }
      @media (min-width: 1600px) {
        font-size: 1.3rem;
      } 
      @media (min-width: 1800px) {
        font-size: 3.2rem;
        top: 25%;
      }
  
  }
  .hero-copy.darkmode{
    color: $white;
  }
  
  .hero-contact{
      display: none;
      width: 8rem;
      height: 3rem;
      position: absolute;
      top: 22%;
      left: 30%;
      border: 2px solid $ice;
      outline: none;
      background-color: $ice;
      color: $white;
      font-weight: 600;
      cursor: pointer;
      &:hover {
        border: 3px solid $ice;
        color: $ice;
        background-color: $white;
      }
  
      @media (min-width: 500px) {
        top: 60%;
        left: 45%;
      }
      @media (min-width: 920px) {
        width: 14rem;
        height: 3rem;
        border-radius: 2rem;
        top: 65%;
        left: 15%;
      }
      @media (min-width: 1600px) {
        left: 25%;
      }
  
  }
  
  .hero-contact.darkmode{
    color: $ice;
    background-color: $nordGrey;
  }
  

  


      }
    
    .hero-content.darkmode{
     
    }


  
   }

   .hero-container.darkmode{
    backdrop-filter: blur(20px) saturate(70%);
    -webkit-backdrop-filter: blur(20px) saturate(71%);
    background-color: rgba(17, 25, 40, 0.17);
    border-radius: 12px;
    border: none;
   
   }
   
 
  
 

  }  













 .hero-wave{
  position: absolute;
  bottom: 0; 
}

.hero-wave.darkmode{
path{
  fill: $nordGrey;
}
}

   


.hero.darkmode{
  background-color: $nordDarkerGrey;

}



