@import './Variables.scss';


.project{
      display: inline;
      width:  290px;
      height: 380px;
      margin: 15% 0 30% 9% ;
      border-radius: none;
      //border: 2px solid red;
      background-repeat: no-repeat;
      cursor: pointer;
      // box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2),
      //   0 6px 20px 0 rgba(0, 0, 0, 0.19);
      background-color: inherit;
      display: flex;
      flex-direction: column;
      position: relative;
      z-index: 2;






      .project-title{
        position: absolute;
        max-width: 80%;
        max-height: 25%;
        top: 1%;
        left: 25%;
        color: $darkGreyFont;

        @media (min-width: 500px) {
         left: 40%;
          
         
        }
      }

    


      .project-img{
        width: 90%;
        height: 50%;
        position: absolute;
        top: 2%;
        left: 5%;
        z-index: 2;
        border-radius: 1.2vh;
        object-fit: cover;
    
      }

       
         
      @media (min-width: 400px) {
        width:  340px;
        height: 400px;
   
      }
   
      @media(min-width: 800px){
        width: 680px;
        height: 380px;
        margin: 5% 0 30% 10% ;
     
      
      }
      @media (min-width: 1025px){
        margin: 10% 0 30% 10% ;
        width: 530px;
        height: 600px;
     
      }
         
   

     

     
      

      

      



}




.project.darkmode{
  background-color: $nordGrey;
  
  p{
    color: $white;
  }

  ul{
    color: $white;
  }
}

.project-button.darkmode {
  background-color: $nordGrey;
}







