@import "./Variables.scss";
@import "./Navbar.scss";
@import "./Hero.scss";
@import "./Projects.scss";
@import "./Footer.scss";





.app{
    width: 100vw;
    height: 4800px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    background-color: $white;
   
    font-family: "Source Code Pro", monospace;
    overflow-x: hidden;

    @media (min-width: 500px) {
     height: 6000px;

      }

      @media (min-width: 880px){
        height: 4600px;
      }
      @media (min-width: 920px) {
        background-color: $arcticGrey;
        height: 4400px;
      }
     
      @media (min-width: 1800px){
        height: 6800px;
      }
}

.app.darkmode{
    background-color: $nordDarkerGrey;

    @media (min-width: 920px){
        background-color: $nordGrey;
    }
   

}