@import './Variables.scss';


.mobile-nav{
    width: 100%;
    height: 3%;
    position: relative;
    
 

    .logo{
        width: 7rem;
        height: 3.9rem;
        position: absolute;
        background-color: $applegrey;
        border-radius: 0.2rem;
        //border: 2px solid red;
        top: 5%;
        left: 5%;
       
    }
    .logo.darkmode{
        background-color: $nordGrey;
      }
  

    .mobile-hamburger{
      width: 4rem;
      height: 4rem;
      outline: none;
      border: none;
      background-color: $white;
      color: $nordGrey;
      text-decoration: none;
      position: absolute;
      top: 0;
      right: 0;
      

      .mobile-icon{
        font-size: 2rem;
      }
    }
    .mobile-hamburger.darkmode{
        background-color: $nordDarkerGrey;
        color: $white;
    }

    .mobile-menu{
        width: 100%;
        height: 70%;
        position: absolute;
        border-bottom: 2px solid #D8D8D8;
        top: 40%;
        z-index: 51;
        line-height: 2;
        

        .mobile-links{
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            margin-left: 42%;
            list-style: none;

         
            .dark{
                margin-left: 10%;
                color: $applegrey;
            }
        }
    }

    .mobile-menu-bg {
        width: 100%;
        height: 100%;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 50;
        background-color: $teal;
    }
}