@import "./Variables.scss";

.projects {
  width: 100vw;
  height: 74%;
  position: relative;
  background-color: $arcticGrey;

  @media (min-width: 500px) {
    height: 85%;
    
  
   }

   @media(min-width: 800px){
    height: 74%;
   }
   @media (min-width: 1025px){
    //height: 74%;
    height: 64%;
   }
  
  .projects-container{
   width: 100%;
   height: 100%;
   position: relative;
   display: flex;
   flex-direction: column;
   justify-content: center;
   margin: 20% 0 0 0;
   //border: 2px solid purple;

   @media (min-width: 500px) {
    flex-direction: column;
    justify-content: flex-start;
   

  }
  @media (min-width: 1025px) {
    flex-direction: row;
    width: 90%;
    margin: 0 auto;
    height: 80%;
    justify-content: flex-start;

   
  }
  @media (min-width: 1600px) {
    height: 90%;
    justify-content: flex-start;
  }

  @media (min-width: 1800px){
    left: 5%;
  }


  a{
    position: relative;
    width: 300px;
    height: 400px;
    margin: 2.5rem;
    z-index: 0;
    border-radius: 0.75rem;
    transition: transform ease-in-out(3s);
   
  
    .project{
      position: relative;
      top: -8%;
      left: -9%;
      background-color: inherit;
      
    }

    &:hover {
      transform: scale(1.1);
     
    }

    @media (min-width: 400px) {
      width:  350px;
      height: 440px;
      margin: 1rem;
 
    }
  
     @media(min-width: 800px){
      width: 700px;
      height: 400px;
    
     
     }
     @media (min-width: 1025px){
      width: 550px;
      height: 620px;
      margin: 2.5rem;
    
     }
  }
  

    .project-container-1 {
      
      width: 100%;
      height: 50%;
      order: 1;
      position: relative;
      display: flex;
      flex-direction: column;
      //border: 2px solid brown;
      justify-content: flex-start;
      

  


   
      
    
      a:first-of-type{
        background: rgb(85,169,229);
        background: linear-gradient(160deg, rgba(85,169,229,1) 6%, rgba(13,110,247,1) 93%);
        backdrop-filter: saturate(180%) blur(20px);
      
        opacity: 80%;
        color: white;
       // transition: all .8s ease-in-out;
  
        h2{
          color: white;
        
        }
        p{
          color: white;
        }
        img{
          width: 100%;
          object-fit: contain;
          margin: -5% 0 0 -5%;
        }
       }

       a:nth-of-type(2){
        background-color: $applegrey;
        opacity: 80%;
        backdrop-filter: saturate(180%) blur(20px);

        h2{
          color: white;
        
        }
        p{
          color: white;
        }
        img{
          width: 100%;
          object-fit: contain;
          margin: -5% 0 0 -5%;
        }
       }


 

     a:last-of-type{
      //background-color: #3D5648;
      background: rgb(66,85,73);
      background: linear-gradient(110deg, rgba(66,85,73,1) 19%, rgba(69,99,80,1) 94%);  
      color: white;

      h2{
        color: white;
      
      }
      p{
        color: white;
      }
      img{
        width: 100%;
        object-fit: contain;
        margin: -5% 0 0 -5%;
      }
      }

      @media (min-width: 500px) {
       flex-direction: column;
        margin: 0 auto;
        
     
       
 
      }
      @media (min-width: 1025px) {
        width: 80%;
        flex-direction: column;
    
        margin: 0 auto;
    
       
      }
      @media (min-width: 1600px) {
        width: 80%;
        flex-direction: column;
        margin: 0 auto;
      }

    }

    .project-container-2{
      width: 100%;
      height: 50%;
      order: 2;
      top: 5%;
      display: flex;
      position: relative;
      flex-direction: column;
     // border: 2px solid aqua;
      justify-content: flex-start;

      @media (min-width: 500px) {
       flex-direction: column;
        margin: 0 auto;
        
       
 
      }
      @media (min-width: 1025px) {
        width: 80%;
        flex-direction: column;
        margin: 0 auto;
    
       
      }
      @media (min-width: 1600px) {
        width: 80%;
        flex-direction: column;
        margin: 0 auto;
      
      }

      a:first-of-type {
        background: rgb(17,50,99);
        background: linear-gradient(110deg, rgba(17,50,99,1) 0%, rgba(34,79,130,1) 62%);
        color: $white;

        p{
          color: $white;
        }

        ul{
          color: $white;
          
        }
      }


      a:nth-of-type(2){
        background: rgb(24,29,31);
        background: linear-gradient(109deg, rgba(24,29,31,1) 16%, rgba(44,51,55,1) 93%);
        opacity: 80%;
        backdrop-filter: saturate(180%) blur(20px);

        h2{
          color: white;
        
        }
        p{
          color: white;
        }
        img{
          width: 100%;
          object-fit: contain;
          margin: -5% 0 0 -5%;
        }
       }

      a:last-of-type{
        background-color: #1A1C1F;
        color: white;
  
        h2{
          color: white;
        
        }
        p{
          color: white;
        }
        img{
          width: 100%;
          object-fit: contain;
          margin: -5% 0 0 -5%;
        }
       }

    }

    .special-project:last-of-type{
      position: relative;
      background-color: #1A1C1F;
      top: -2%;
      //border: 2px solid magenta;
      order: 4;

       h2{
        color: white;
       }

       p{ 
       color: white;
       }
      .special-demo-button{ 
       background-color: inherit;
       color: white;
       border: 2px solid white;


        } 
        ul{
          color: white;
        }

      @media (min-width: 500px) {
       top: 0;
       
 
      }
      @media (min-width: 920px) {
        top: -5%;
    
       
      }
      @media (min-width: 1600px) {
        top: -5%;
      
      }

      &:hover{
       .special-demo-button{
        background-color: white;
        color: #1A1C1F;
       }
       }

    }
  }
 



 
 


  .project-wave{
    position: absolute;
    bottom: 0;
    

    @media (min-width: 920px){
      top: 114%;
    }
    @media (min-width: 1025px){
      top: 112%;
    }
  }
  
  .project-wave.darkmode{
    path{
        fill: $nordDarkerGrey;
    }
  }


  
 
}




.projects.darkmode{
  background-color: $nordGrey;
}



