@import "./Variables.scss";

body{
    background-color: $applegrey;
}
.Info-exit{
    position: absolute;
    top: 5%;
    left: 1%;
    width: 7rem;
    height: 3rem;
    border-radius: 2rem;
    text-decoration: underline;
    background-color: $arcticGrey;
    color: $darkGreyFont;
    opacity: 0.7;
    font-size: 2rem;
    font-weight: thin;
    border: 2px solid $arcticGrey;
      

  
    &:hover{
        background-color: $arcticGrey;
        opacity: 1;
        cursor: pointer;
        opacity: 1;
    }


}
.Info-exit.darkmode{
    background-color: $nordGrey;
    color: $arcticGrey;
    border: none;
}

.Info-nav{
    position: fixed;
    top: 5%;
    right: 4%;
    display: none;
    //border: 2px solid red;
    cursor: pointer;
    list-style-type: none;
    font-family: "Inter", sans-serif;
    color: $darkGreyFont;
    opacity: 0.8;
    font-weight: 400;
    font-size: 0.8rem;
    line-height: 40px;
  
     li{
        &:hover{
            text-decoration: underline;
            opacity: 0.6;
        }
     }

   
 
     @media (min-width: 1025px){
        display: block;
        position: fixed;
        top: 5%;
        right: 4%;
        //border: 2px solid red;
        cursor: pointer;
        list-style-type: none;
        font-family: "Inter", sans-serif;
        color: $darkGreyFont;
        opacity: 0.8;
        font-weight: 400;
        font-size: 0.8rem;
        line-height: 40px;
 
     }
 
}

.Info-nav.darkmode{
    color: white;
}


.Info-container{
 background-color: $white;
 width: 100%;
 height: 200%;
 margin: 0 auto;
 position: absolute;
 top: 10%;
 display: flex;
 flex-direction: column;
 justify-content: space-around;

    .Info-header{
      width: 100%;
      height: 10%;
      position: absolute;
      top: 0;
      //border: 2px solid red;
    }
    .Info-header.darkmode{
        color: $white;
    }

    .Info-desc{
        width: 100%;
        height: 20%;
        position: absolute;
        top: 10%;
        margin: 0 auto;
        //border: 2px solid green;

        .desc-container{
            width: 80%;
            height: 50%;
            position: relative;
            top: 0;
            left: 10%;
          //  border-top: 2px solid $applegrey;
            //border-bottom: 2px solid $applegrey;

            .Project-info-title{
                position: absolute;
                top: -40%;
                font-family: "Roboto Condensed", sans-serif;
                font-size: 3rem;
                font-weight: 500;
                padding: 0.5rem;
                line-height: 46px;
                color: #3B3D41;

    
                @media (min-width: 1025px){
                    top: 5%;
                    font-size: 3rem;
                    font-weight: 500;
                    padding: 0.5rem;
                    line-height: 46px;
            
                }
        

            }
            .Project-info-title.darkmode{
                color: $white;
            }
            .Project-info-synopsis{
                position: absolute;
                top: 5%;
                font-family: "Roboto Condensed", sans-serif;
                font-size: 1.2rem;
                font-weight: 500;
                padding: 0.2rem;
                line-height: 26px;
                color: #A1A5AB;

                @media (min-width: 1025px){
                    top: 20%;
               
                }
            
                

            }
          
            .Project-info-desc{
                position: absolute;
                border-top: 2px solid $applegrey;
                padding: 0.5rem;
                top: 40%;
                font-family: "Inter", sans-serif;
                font-weight: 400;
                font-size: 1rem;
                line-height: 30px;
                color: $darkGreyFont;
                @media (min-width: 500px){
        

                }
            
                @media (min-width: 1025px){
                    top: 40%;
                    line-height: 40px;
               
                }
            
        
                

            }
            .Project-info-desc.darkmode{
                color: $white;
            }



            @media (min-width: 1025px){
                border-bottom: 2px solid $applegrey;
           
            }
        
    

        
        }
        .Table-container{
            width: 80%;
            position: absolute;
            top: 180%;
            left: 10%;
           // border: 2px solid red;
              .Table{
                width: 100%;
               // border: 2px solid red;
                font-family: "Inter", sans-serif;
                font-weight: 400;
                font-size: 0.9rem;
                line-height: 20px;
                color: $darkGreyFont;
                text-decoration: none;

                .Table-titles{
                    font-weight: 600;
                    
                }
                .Table-titles.darkmode{
                    color: #A1A5AB;
                }
                .Table-info.darkmode{
                    color: $white;
                }

                td{
                    list-style-type: none;
                }
              
             
            
                @media (min-width: 1025px){
                    font-weight: 400;
                    font-size: 1rem;
                    line-height: 30px;
               
                }
            
              
                
              }
              @media (min-width: 500px){
                top: 80%;
        

              }
          
              @media (min-width: 1025px){
                width: 80%;
                top: 55%;
                left: 10%;
                  
              }
          
           
              
          }


    }

    .Info-main{
        width: 100%;
        height: 40%;
        position: absolute;
        top: 60%;
        border-top: 2px solid $applegrey;
  
        @media (min-width: 500px){
            top: 30%;
    
        }
    
        @media (min-width: 1025px){
            top: 30%;
            
        }
    

        .Info-card{
            width: 75%;
            height: 300px;
            border-radius: 0.7rem;
            border: 1px solid $arcticGrey;
            position: absolute;
            top: 50%;
            left: 12%;
            background-color: $applegrey;
            opacity: 0.8;
            backdrop-filter: saturate(180%) blur(20px);

            @media (min-width: 500px){
                top: 10%;
    
            }
        
            @media (min-width: 1025px){
                top: 5%;
                height: 600px;
                
            }
        
          

        }

    }

 

    @media (min-width: 1025px){
        width: 80%;
        height: 100%;
        margin: 0 auto;
        position: relative;
        top: 0;
        display: flex;
        flex-direction: column;
        justify-content: space-around;

    }


}

.Info-container.darkmode{
    background-color: $nordDarkerGrey;
    
}