@import "./Variables.scss";

*,
*:before,
*:after{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    width: 100vw;
    height: 100vh;
}

.navbar{
  background-color: $white;
    width: 100vw;
    height: 3%;
    display: flex;
    justify-content: space-evenly;
    align-items: baseline;
    position: relative;
    top: 0;
    font-family: $mainFont;
    //border-bottom: 2px solid $applegrey;
    
   

   

    .logo{
        
       // background-image: url("../Assets/Portfolio\ Logo\ LightMode.svg");
    
        background-color: $applegrey;
        opacity: 0.8;
        backdrop-filter: saturate(180%) blur(20px);
        background-size: contain;
        background-repeat: no-repeat;
        width: 10rem;
        border-radius: 0.2rem;
        height: 3.4rem;
        position: absolute;
        top: 12%;
        left: 3%;
        order: 1;

        .logo-img{
          position: absolute;
          background-image: url("../Assets/Ronan\ Goligy.logo.svg");
          background-size: contain;
          background-repeat: no-repeat;
          top: 5%;
          left: 5%;
          width: 10rem;
          height: 3rem;
          
        }
        .logo-img.darkmode{
          background-image: url("../Assets/Ronan\ Goligy.logo.dark.svg");
        }
    }
    .logo.darkmode{
      background-color: $nordGrey;
    }

    .desk-menu {
        @media (min-width: 600px) {
            width: 50%;
            order: 2;
            display: flex;
            justify-content: space-around;
            list-style: none;
            position: absolute;
            top: 15%;
            right: 0;
            z-index: 11;
          
           

            .dark{
              background-color: transparent;
              outline: none;
              border: none;
              cursor: pointer;
            }

          
    
            li {
              font-family: "Inter", sans-serif;
              font-weight: 400;
              padding: 0.4rem;
              border-radius: 0.2rem;
              font-size: 1rem;
              color: $darkGreyFont;
              &:hover {
                background-color: $applegrey;
             
                opacity: 0.8;
                
              }
              @media (min-width: 1100px) {
                font-size: 1rem;
              }
              @media (min-width: 1600px) {
                font-size: 1.2rem;
              }
            }
            a {
              text-decoration: none;
            }
          }

          @media (min-width: 920px){
            width: 40%;
            top: 20%;
            
            
          }

          @media (min-width: 1025px){
            width: 30%;
          }
    }
}

.navbar.darkmode{
  background-color: $nordDarkerGrey;

  li{
    color: $arcticGrey;
  }
}

